<template>
        <div>
                <h2></h2>
                <el-row>
                        <el-col :span="3"></el-col>
                        <el-col :span="10">{{name}}</el-col>
                </el-row>
                <el-row>
                        <el-col :span="3"></el-col>
                        <el-col :span="10">{{remark}}</el-col>
                </el-row>
                <el-row>
                        <el-col :span="3"></el-col>
                        <el-col :span="10">{{created}}</el-col>
                </el-row>
                <el-row>
                        <el-col :span="3"></el-col>
                        <el-col :span="10">{{status}}</el-col>
                </el-row>
          <el-table
                :data="tableData"
                border
                style="width: 100%">
                <el-table-column
                  prop="id"
                  label=""
                  :span="3">
                </el-table-column>
                <el-table-column
                  prop="runUser"
                  label=""
                  :span="3">
                </el-table-column>
                <el-table-column
                  prop="remark"
                  label=""
                  :span="3">
                </el-table-column>
                <el-table-column
                  prop="cmd.cmd"
                  label=""
                  :span="3">
                </el-table-column>
                <el-table-column
                  prop="created"
                  label=""
                  :span="3">
                </el-table-column>
                <el-table-column
                  prop="status"
                  label=""
                  :span="3">
                  <template scope="data">
                      {{data.row.status?'':''}}
                  </template>
                </el-table-column>
                <el-table-column fixed="right" label="" width="180">
                  <template scope="scope">
                      <el-button type="text" @click="handleDelete(scope.$index,scope.row)"><i class="el-icon-delete" /></el-button>
                      <router-link :to="{name:'edit_command',params:{name:$route.params.name,id:scope.row.id}}"><el-button type="text" size="small"><i class="el-icon-edit" /></el-button></router-link>
                  </template>
                </el-table-column>
          </el-table>
          <el-row style="margin-top:20px">
                <router-link :to="{name:'add_command',params:{name:name}}"><el-button type="primary"></el-button></router-link>
                <router-link to="/index/plan_list"><el-button></el-button></router-link>
          </el-row>
         </div>
</template>

<script>
  export default {

    methods: {
      switchPlan(){

      },
      handleDelete(index,row){
        if( confirm('') ){
          http.post('/cron/destroy',{
            plan_name:this.$route.params.name,
            id:row.id,
          })
          .then((res)=>{
            if(res.data.statusCode==10000){
              this.tableData.splice(index,1);
            }
            if(res.data.statusCode==40002){
                  this.$router.push('/login');
            }
          })
        }

      }
    },
    
    created(){
        http.get('/plan',{
          params:{
            name:this.$route.params.name,
          }
        })
        .then((res)=>{
          if(res.data.statusCode==10001){
                let data = res.data.arrData;
                // data = {
                //     name:'test',
                //     created:"123",
                //     remark:123,
                //     status:true,
                //     cron_list:[{
                //         name:'',
                //         runUser:'cmd',
                //         remark:'test',
                //         cmd:'123',
                //         created:'123456',
                //         status:true
                //     }]
                // }

                this.name = data.name;
                this.created = data.created;
                this.remark = data.remark;
                this.status = data.status;
                this.tableData = objectToArray(data['cmd-list']);
          }
        })
    },
    data() {
      return {
        tableData: [],
        created:"",
        name:"",
        remark:"",
        status:'',
      }
    }
  }
  </script>


